  transition: all .3s;
}

.slider-pag__prev:hover, .slider-pag__next:hover {
  color: #929292;
}

.slider-pag__progressbar {
  width: 100%;
}

.slider-pag__progressbar .swiper-pagination-progressbar-fill {
  background-color: #05c2df;
  height: 1px;
}

.slider-pag .swiper-pagination-progressbar {
  background: #212121;
  height: 1px;
  position: absolute;
  top: 30px;
  right: 0;
}

.swiper-button-disabled {
  color: #929292;
}

