.select {
  position: relative;
}

.select__inner {
  cursor: pointer;
  background-color: #191536;
  width: 100%;
  padding: 13px 24px;
  position: relative;
}

.select__inner:after {
  content: "";
  background: url("down.09ac9dd7.svg") no-repeat;
  align-self: center;
  width: 9px;
  height: 6px;
  margin-bottom: 5px;
  margin-left: 5px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%);
}

.select__inner--small {
  background-color: #0000;
  padding: 0;
}

.select__inner--small:after {
  right: -20px;
}

.select__current {
  color: #fff;
  align-items: center;
  display: flex;
}

.select__current--blue {
  color: #05c2df;
}

.select._active .select__list {
  opacity: 1;
  visibility: visible;
}

.select__list {
  opacity: 0;
  visibility: hidden;
  bottom: inherit;
  right: inherit;
  z-index: 5;
  background-color: #191536;
  border-radius: 4px;
  min-width: 100%;
  max-height: 250px;
  margin-top: 3px;
  padding: 0 10px;
  transition: all .3s;
  position: absolute;
  overflow: hidden auto;
}

.select__list--center {
  max-height: 111px;
  margin-top: 14px;
  padding: 0;
}

.select__item {
  padding: 6px 12px;
}

.select__item a {
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: inline-block;
}

.select__item a:hover {
  color: #05c2df;
}

.select__list--center {
  transform: translateX(-15%);
}

