    background-color: #bdccff;
    max-width: 100%;
    height: 70px;
    transition: top .3s;
    position: fixed;
    top: -100%;
    left: 0;
  }

  .menu-open .menu__body:before, .menu-open .menu__body {
    top: 0;
    transform: translateY(0%);
  }
}

