.icon-menu {
  display: none;
}

@media (width <= 768px) {
  .icon-menu {
    z-index: 5;
    flex: 0 0 24px;
    height: 15px;
    display: block;
    position: relative;
  }
}

@media (width <= 480px) {
  .icon-menu {
    flex: 0 0 20px;
    height: 11px;
  }
}

@media (width <= 768px) {
  .icon-menu span, .icon-menu:before, .icon-menu:after {
    content: "";
    background-color: #fff;
    width: 100%;
    height: 1px;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .icon-menu span {
    width: 20px;
    top: calc(50% - .5px);
    left: -5px;
  }

  .icon-menu:before {
    top: 0;
  }

  .icon-menu:after {
    bottom: 0;
  }

  .menu-open .icon-menu span {
    width: 0;
  }

  .menu-open .icon-menu:before {
    top: 50%;
    transform: rotate(-45deg);
  }

  .menu-open .icon-menu:after {
    bottom: calc(50% - 1.2px);
    transform: rotate(45deg);
  }
}

